@import "../partials/variables";

//Theme 15
#atw_style15{
    .atw__title15{
        padding: 20px;
        margin-bottom: 35px;
        background: #fff;
        &:before{
            content: none;
        }
        h3{
            text-align: left;
        }
    }
    .atw--single_item{
        background: #fff;
        padding: 20px;
        min-height: 355px;
        position: relative;
        overflow: hidden;
        transition: $transition;
        &:hover{
            box-shadow: 0 10px 10px rgba(134,152,164,0.1);
            .atw_item_bottom{
                bottom: 20px;
                .atw_product_actions2{
                    visibility: visible;
                    opacity: 1;
                    margin-top: 0;
                }
            }
        }
        .atw_item_top{
            .badge--right{
                right: 0;
                top: 0;
                .atw_badge--right{
                    background: $theme2;
                    border-radius: 0;
                }
            }
        }
        .atw_item_bottom{
            background: #fff;
            padding-bottom: 5px;
            position: absolute;
            left: 0;
            width: 100%;
            bottom: -35px;
            transition: $transition;
            .atw_item_header{
                padding: 20px 0 0 0;
                text-align: center;
            }
            .atw_item_info{
                text-align: center;
                margin: 10px 0 15px;
                span{
                    display: block;
                    &.atw_price{
                        margin-bottom: 10px;
                        color:#9192a3;
                        del{
                            color: $theme2;
                            display: inline-block;
                            margin-left: 2px;
                        }
                    }
                }
            }
            .atw_product_actions2{
                visibility: hidden;
                opacity: 0;
                margin-top: 15px;
                li{
                    margin-right: -1px;
                    a{
                        border-radius: 0;
                    }
                }
            }
        }
    }
    .atw_slider_controls{
        .slider--control15{
            position: absolute;
            right: 0;
            margin: 0 4px 0 4px;
            top: 14px;
            transform: none;
            border-radius: 3px;
            transition: 0.3s ease;
            border: 1px solid $borderColor;
            &:hover{
                border-color: $theme2;
                color: #fff;
                background: $theme2;
            }
            &.icon-arrow-left{
                left: auto;
                right: 80px;
            }
            &.icon-arrow-right{
                right: 31px;
            }
        }
    }
}